<template>
  <div class="main" id="main_skills">
    <div class="custom-shape-divider-top-1695977420">
      <svg data-name="Layer 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 120"
        preserveAspectRatio="xMinYMin meet">
        <path
          d="M321.39,56.44c58-10.79,114.16-30.13,172-41.86,82.39-16.72,168.19-17.73,250.45-.39C823.78,31,906.67,72,985.66,92.83c70.05,18.48,146.53,26.09,214.34,3V0H0V27.35A600.21,600.21,0,0,0,321.39,56.44Z"
          class="shape-fill"></path>
      </svg>
    </div>
    <div id="skills" class="skills p-4 p-md-5 mb-4 rounded text-body-emphasis">
      <h1 class="display-4 fst-italic text-start text-white">Skills</h1>
      <br>
      <table class="table table-responsive">
        <tbody>
          <tr v-for="(skill, index) in skills" :key="index" @mouseenter="highlightRow(index)"
            @mouseleave="unhighlightRow()">
            <th scope="row" class="col-1  text-white">{{ index + 1 }}</th>
            <td class="col-4 text-start  text-white">{{ skill.category }}</td>
            <td class="col-7 text-end  text-white">
              <div class="d-flex justify-content-end align-items-center">
                <div v-for="(icon, iconIndex) in skill.icons" :key="iconIndex" class="icon-container"
                  @mouseenter="showAltText(icon.alt)" @mouseleave="hideAltText()">
                  <img :src="icon.src" :alt="icon.alt" :width="icon.width" />
                  <div v-if="showAlt" class="alt-text">{{ icon.alt }}</div>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="spacer layer1"></div>
  </div>
</template>



<style scoped>
#main_skills {
  position: relative;
}

.skills {
  padding-top: 180px !important;
}

.custom-shape-divider-top-1695977420 {
  position: absolute;
}

.custom-shape-divider-top-1695977420 svg {
  display: block;
  width: calc(101% + 1.3px);
  height: 160px;
  transform: rotateY(180deg);
}

.custom-shape-divider-top-1695977420 .shape-fill {
  fill: #F8F9FA;
}

.table-responsive {
  overflow-x: auto;
}

.icon-container {
  position: relative;
  margin-right: 10px;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

.icon-container:hover {
  transform: scale(1.2);
}

.alt-text {
  position: absolute;
  bottom: -25px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 14px;
  white-space: nowrap;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.icon-container:hover .alt-text {
  opacity: 1;
}

td,
th {
  /* background-color: #f6fffc !important; */
  background-color: #010014;
  vertical-align: middle;
  font-size: large;
}

tr {
  height: 120px;
}


.spacer {
  aspect-ratio: 900/400;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  /* position: absolute;
  top: 10; */
}


.layer1 {
  background-image: url('./layered-waves-haikei.svg');
}


@media screen and (max-width: 600px) {
  .icon-container {
    margin-right: 0;
  }

  .alt-text {
    bottom: -50px;
  }

  td {
    display: block;
    padding-bottom: 20px;
  }

  .icon-container {
    display: block;
    text-align: left;
    /* Align icons to the left under each category name */
    margin: 0 auto;
    margin-top: 10px;
  }

  .icon-container img {
    width: 50px;
  }
}
</style>


<script>
export default {
  data() {
    return {
      skills: [
        {
          category: "Front End",
          icons: [
            { src: "./img/HTML.png", alt: "HTML", width: "60" },
            { src: "./img/css.png", alt: "CSS", width: "60" },
            { src: "./img/JS.png", alt: "JavaScript", width: "65" },
            { src: "./img/vuejs.png", alt: "VueJS", width: "65" },
            { src: "./img/angularjs.png", alt: "AngularJS", width: "60" },
            { src: "./img/react.png", alt: "React", width: "55" },
          ],
        },
        {
          category: "Back End",
          icons: [
            { src: "./img/flask_white.png", alt: "Flask", width: "80" },
            { src: "./img/nodejs.png", alt: "NodeJS", width: "80" },
            { src: "./img/mongodb.png", alt: "MongoDB", width: "70" },
          ],
        },
        {
          category: "Programming",
          icons: [
            { src: "./img/python.png", alt: "Python", width: "60" },
            { src: "./img/c++.png", alt: "C++", width: "60" },
            { src: "./img/c.png", alt: "C Language", width: "60" },
            { src: "./img/java.png", alt: "Java", width: "50" },
            { src: "./img/r.png", alt: "R", width: "60" },
            { src: "./img/matlab.png", alt: "MatLab", width: "90" },
          ],
        },
        {
          category: "Tools",
          icons: [
            { src: "./img/docker.png", alt: "Docker", width: "60" },
            { src: "./img/git.png", alt: "Git", width: "55" },
            { src: "./img/postman.png", alt: "Postman", width: "75" },
            { src: "./img/staruml.png", alt: "StarUML", width: "60" },
          ],
        },
      ],
      highlightedRow: null,
      showAlt: false,
      altText: "",
    };
  },
  methods: {
    highlightRow(index) {
      this.highlightedRow = index;
    },
    unhighlightRow() {
      this.highlightedRow = null;
      this.hideAltText();
    },
    showAltText(alt) {
      this.showAlt = true;
      this.altText = alt;
    },
    hideAltText() {
      this.showAlt = false;
      this.altText = "";
    },
  },
};
</script>